            <div class="main_register m-auto ">
                <div class="logo auto m-auto d-flex justify-content-center p_1r"><img src="@/assets/images/logo705.png"></div>
                <div class="title auto m-auto d-flex justify-content-center p_1r "><strong>Mu ng K Dnh Cho i Tc Bnh Vin</strong></div>
                <div class="register">
                    <div class="infor p_1r mlr_1-5r">
                        <h5>Mc tiu ca EHR MedicalBlock</h5>
                        <p>Nhm to iu kin  qun l h s bnh n mt cch khoa hc hn,
                            chng ti lu tr thng tin bnh nhn di dng thng tin in t c th d dng qun l
                            v thun li hn trong qu trnh khm cha bnh. Cc y bc s c quyn truy cp gii hn
                            vo h s bnh nhn  hiu r tnh hnh bnh t  a ra cch iu tr ph hp nht v
                            b qua cc xt nghim c bn khng cn thit. </p>

                        <p>Thng tin bnh nhn c lu li sau mi ln khm cha bnh bi cc y bc s lin kt vi h thng
                            ca chng ti. V th vi mong mun qun l h s bnh nhn hiu qu hn chng ti cn s hp tc
                            ca cc bn.</p>
                        
                        <h5>Chng ti cung cp</h5>
                        <ul>
                            <li>
                                H thng h s bnh n vi giao din thn thin v thng minh, gip d dng s dng nng cao hiu qu lm vic.
                            </li>

                            <li>
                               Thng tin c bn ca bnh nhn c xc thc bng blockchain, nh  c th b qua cc xt nghim c bn khng cn thit,
                               n c bit hu ch trong tnh hung khn cp.
                            </li>

                            <li>
                                Truy cp 24/7 vo h s chm sc sc khe ca bnh nhn, thng qua my tnh hoc thit b di ng ca bn.
                            </li>                          
                        </ul>
                        <h6>Hng dn in vo form ng k/s dng</h6>
                         <p>sau khi in vo n ng k ti khon email v password ca bn s c dng  ng nhp vo h thng ca chng ti,
                            bn - ngi qun tr vin s  cp mi, qun l ti khon ca cn b bc s</p>
                        <p class="pt-3">Hy tham gia cng chng ti bng cch in vo mu sau.</p>
                    </div>
                        <div class="formCus p_1r mlr_1-5r">
                            <div class="block">Tn Bnh Vin/C Quan</div>
                            <div class=" Custominput3 pr2"    >
                                <input type="text" style="width:100%;"  v-model="name_hospital">
                            </div>
                            <div class="block">S in Thoi/Fax</div>
                            <div class=" Custominput3 pr2"    >
                                <input type="text"  style="width:100%;"  v-model="phone_hospital">
                            </div>
                            <div class="block">a Ch</div>
                            <div class=" Custominput3 pr2"    >
                                <input type="text"  style="width:100%;" v-model="address_hospital" >
                            </div>
                            <div class="block">a Ch Email</div>
                            <div class=" Custominput3 pr2"    >
                                <input type="text"  style="width:100%;"  v-model="email_hospital">
                            </div>
                            <div class="block">Mt Khu</div>
                            <div class=" Custominput3 pr2"   >
                                <input type="password"  style="width:100%;"  v-model="password_hospital" >
                            </div>
                            <div class=" Custominput3 pr2 agree">
                               <input type="checkbox" v-model="toggle" id="agree" >
                                <span id="infor-agree">Bng cch ng k vo biu mu ny, ti ng  choEHR MedicalBlock lin h  xc thc
                                    v cam kt thc hin ng nh chnh sch v iu khon.
                                </span> 
                            </div>
                            <br>
                            <div class="danger-alert" v-html="error" />
                            <div v-show="show_error">
                                <p v-if="!toggle">
                                    <b  class="danger-alert">Vui lng tch vo  cam kt</b>
                                </p>
                            </div>                      
                            <div class="agree_btn btn"  @click.prevent="checkForm" > NG </div>                                                                                   
                        </div>             
                </div>
            </div>
        </form>        
    </div>
</template>

<script>
import AuthenticationService from '@/services/AuthenticationService'
export default {
    data(){
        return{
            show_error:false,
            id_account:'',
            roles:2,
            error:''
        }
    },
    computed:{
        toggle: {
           get(){ 
               return this.$store.getters.toggle
            },
           set(payload){  
                this.$store.dispatch("toggle", payload);
           }
        },
        name_hospital: {
           get(){
               return this.$store.getters.name_hospital
            },
           set(payload){  
                this.$store.dispatch("updateInformationHospital",{
                    payloadValue: payload,
                    payloadRouter:'name_hospital'
                } );
           }
        },
       phone_hospital: {
           get(){
               return this.$store.getters.phone_hospital
            },
           set(payload){  
                this.$store.dispatch("updateInformationHospital",{
                    payloadValue: payload,
                    payloadRouter:'phone_hospital'
                } );
           }
       },
       address_hospital: {
           get(){
               return this.$store.getters.address_hospital
            },
           set(payload){  
                this.$store.dispatch("updateInformationHospital",{
                    payloadValue: payload,
                    payloadRouter:'address_hospital'
                } );
           }
       },
       email_hospital: {
           get(){
               return this.$store.getters.email_hospital
            },
           set(payload){  
                this.$store.dispatch("updateInformationHospital",{
                    payloadValue: payload,
                    payloadRouter:'email_hospital'
                } );
           }
       },
        password_hospital: {
           get(){
               return this.$store.getters.password_hospital
            },
           set(payload){  
                this.$store.dispatch("updateInformationHospital",{
                    payloadValue: payload,
                    payloadRouter:'password_hospital'
                } );
           }
       },
       
    }, 
    methods: {
            checkForm:function(e) {
                if(!this.toggle){
                    this.show_error = true;
                    e.preventDefault();
                }
                else
                    this.register()           
            },
        async register () {   
                try {
                    const response = await AuthenticationService.RegisterHospitalRequest({
                    email: this.email_hospital,
                    password: this.password_hospital,
                    name_hospital:this.name_hospital,
                    phone_hospital:this.phone_hospital,
                    address_hospital:this.address_hospital
                    })
                }catch (error) {
                    this.error = error.response.data.error
                }
                this.show_error = false
                this.$store.dispatch('resetRegiserHospital')
                this.$router.push({
                    name: 'thanks'
                })
        },
    }
}
</script>

<style scoped>

