                        <p>Chng ti pht trin trang web nhm ci thin tnh hnh qun l h s bnh n,
                            gim bt lo lng mi ln bn qun mang s khm bnh khi ti khm,
                            hoc mt s khm bnh iu ny khin cc bc s s khng hiu r ht tnh
                            trng bnh ca bn  a ra phng php cha tr tt nht cng nh toa thuc ph hp.
                            Hn na him ai cn thn gi li ton b h s khm tt c cc bnh ca mnh v cng
                            khng ngh rng cc bnh c lin quan n nhau  kp thi thng bo cho bc s cha tr</p>

                        <p>Gi y EHR MedicalBlock hon ton c th gip bn gii quyt vn d ny. Bng cch lu tt c tnh trng sc khe ca bn di 1 h s mang tn bn, khi c bn cp quyn xem h s , tt c cc bc s bnh vin lin kt vi chng ti u c th tham vn tnh trng sc khe ca bn  a ra phng php v
                            toa thuc ph hp nht vi bn, c bit l phng php cha tr kt hp nhiu loi bnh</p>

                        <p>Chng ti ang h tr s pht trin ca mt h thng chm sc sc khe c nhn nhanh hn, ng tin cy hn v cung cp cho bn kt qu tt hn. Bn hon ton c th truy cp h s
                             ca mnh bt c lc no ti bt k u min l c internet. c bo v bng m kha an toan, an hy yn tm rng thng tin ca bn c bo mt hon ton, tr khi bn cp quyn cho bc s ca bn c xem h s ca bn.</p>
                        <p>Hy ng k cng chng ti bng cch in vo mu sau:</p>
                    </div>
                        <div class="formCus p_1r mlr_1-5r">
                            <div class="block">Tn</div>
                            <div class=" Custominput pr2"    >
                                <input type="text" placeholder="H v Tn Lt" style="width:100%;"  v-model="firstname">
                            </div>
