</template>

<script>
import AuthenticationService from '@/services/AuthenticationService'
import { mapGetters} from "vuex";
export default {
    data(){
        return{
            id_account:'',
            roles:1
        }
    },
    computed:{
         ...mapGetters(
      ["url"]),
      error: {
           get(){ 
               return this.$store.getters.error
            },
           set(payload){  
                this.$store.dispatch("error", payload);
           }
        },
        checked: {
           get(){ 
               return this.$store.getters.checked
            },
           set(payload){  
                this.$store.dispatch("checked", payload);
           }
        },
        right: {
           get(){
               return this.$store.getters.right
            },
           set(payload){
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'right'
                } );
           }
        },
        cmnd: {
           get(){
               return this.$store.getters.cmnd
            },
           set(payload){
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'cmnd'
                } );
           }
        },
        firstname: {
           get(){
               return this.$store.getters.firstname
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'firstname'
                } );
           }
        },
        lastname: {
           get(){
               return this.$store.getters.lastname
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'lastname'
                } );
           }
        },
        day: {
           get(){
               return this.$store.getters.day
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'day'
                } );
           }
        },
        month: {
           get(){
               return this.$store.getters.month
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'month'
                } );
           }
       },
        year: {
           get(){
               return this.$store.getters.year
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'year'
                } );
           }
       },
       phone: {
           get(){
               return this.$store.getters.phone
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'phone'
                } );
           }
       },
       sex: {
           get(){
               return this.$store.getters.sex
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'sex'
                } );
           }
       },
       address: {
           get(){
               return this.$store.getters.address
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'address'
                } );
           }
       },
       email: {
           get(){
               return this.$store.getters.email
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'email'
                } );
           }
       },
        password: {
           get(){
               return this.$store.getters.password
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'password'
                } );
           }
       },
       valueRight: {
           get(){
               return this.$store.getters.valueRight
            },
           set(payload){  
                this.$store.dispatch("updateInformation",{
                    payloadValue: payload,
                    payloadRouter:'valueRight'
                } );
           }
       },

    }, 
    methods: {
        onFileChange(e) {
            this.$store.dispatch("onFileChange", e);
        },
        async register () {
            if(!this.id_account){
                try {
                    const response = await AuthenticationService.register({
                    email: this.email,
                    password: this.password,
                    roles:this.roles
                    })
                    this.id_account = response.data.user.id
                    this.updatedInforUser();
                    this.$store.dispatch('setToken', response.data.token)
                    this.$store.dispatch('setUser', response.data.user)
                }catch (error) {
                    this.error = error.response.data.error
                    }
            }else{
                this.updatedInforUser();
            }
        },
        async updatedInforUser(){
            try {
                const response = await AuthenticationService.patient({
                    id_account:this.id_account,
                    firstname:this.firstname,
                    lastname:this.lastname,
                    day:this.day,
                    month:this.month,
                    year:this.year,
                    address:this.address,
                    sex:this.sex,
                    cmnd:this.cmnd,
                    right:this.valueRight,
                    phone:this.phone,
                    image:this.url
                })
                this.$store.dispatch("resetRegiserPatient")                
                this.$router.push({
                        name:'patient',
                        params:{id: this.id_account}
                })

            }catch (error) {              
                this.error = error.response.data.error
                }
        }
    }
}
</script>

